<template>
    <div class="confirmOrder">
        <header>
            <a href="javascript:history.back(-1)" class="h_a_1">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span class="h_s_1">确认订单</span>
            <router-link to="/login" v-if="isShow1" class="h_a_2">
                <span>登录|注册</span>
            </router-link>
            <router-link to="/profile" v-if="!isShow1" class="h_a_2">
                <i class="el-icon-user"></i>
            </router-link>
        </header>
        <section class="s1">
            <router-link to="/chooseAddress" class="s11" v-if="isShow3">
                <div>
                    <i class="el-icon-location-outline"></i>
                    <div>请添加一个收货地址</div>
                </div>
                <i class="el-icon-arrow-right"></i>
            </router-link>
            <router-link to="/chooseAddress" class="s11" v-if="!isShow3">
                <div>
                    <i class="el-icon-location-outline"></i>
                    <div class="s12">
                        <div class="s121">
                            <span>{{address.name}}</span><span>{{address.sex == 1?"先生":"女士"}}</span><span>{{address.phone}}</span>
                        </div>
                        <div class="s122">
                            <span>{{address.tag}}</span>
                            <p>{{address.address}}</p>
                        </div>
                    </div>
                </div>
                <i class="el-icon-arrow-right"></i>
            </router-link>
        </section>
        <section class="s2">
            <p>送达时间</p>
            <div>
                <p>尽快送达 | 预计 {{this.service_time}}</p>
                <p>{{shop.delivery_mode.text}}</p>
            </div>
        </section>
        <section class="s3">
            <div class="s31" @click="isShow2 = !isShow2">
                <span>支付方式</span>
                <div class="s311">
                    <span>在线支付</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </div>
            <div class="s32">
                <span>红包</span>
                <span>暂时只在饿了么APP中支持</span>
            </div>
        </section>
        <div class="yinying" v-if="isShow2" @click="isShow2 = !isShow2"></div>
        <div class="styleM" v-if="isShow2">
            <div class="st1">支付方式</div>
            <div class="st2"><span>货到付款（商家不支持货到付款）</span><i class="el-icon-success"></i> </div>
            <div class="st3"> <span>在线支付</span><i class="el-icon-success" @click="isShow2 = !isShow2"></i></div>
        </div>
        <section class="s4">
            <div class="s41">
                <img :src="'https://elm.cangdu.org/img/'+this.shop.image_path" class="img">
                <span>{{shop.name}}</span>
            </div>
            <ul>
                <li v-for="s in shopsC">
                    <p>{{s.name}}{{s.specs != "" ? ('-'+s.specs):""}}</p>
                    <div class="s411">
                        <span>x{{s.quantity}}</span>
                        <span>￥{{s.price}}</span>
                    </div>
                </li>
            </ul>
            <div class="s42">
                <p>餐盒</p>
                <div><span></span><span>￥351</span></div>
            </div>
            <div class="s42">
                <p>配送费</p>
                <div><span></span><span>￥{{shop.float_delivery_fee}}</span></div>
            </div>
            <div class="s43">
                <p>订单￥{{total}}</p>
                <div><span>待支付￥{{total}}</span></div>
            </div>
        </section>
        <section class="s5">
            <router-link to="/remark">
                <span>订单备注</span>
                <div class="s51">
                    <span class="ellipsis">{{remark}}</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </router-link>
            <router-link to="/invoice">
                <span>发票抬头</span>
                <div class="s51">
                    <span>不需要开发票</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </router-link>
        </section>
        <section class="s6">
            <p class="s61">待支付￥{{total}}</p>
            <p class="s62" @click="order">确认下单</p>
        </section>
    </div>
</template>

<script>
    export default {
        name: "ConfirmOrder",
        data(){
            return{
                isShow1:true,
                shop:[],
                shops:[],
                shopsC:[],
                user_id:"",
                cart_id:"",
                service_time:"",
                Hours:"",
                Minutes:"",
                isShow2:false,
                total:"",
                isShow3:true,
                address:[],
                entities:[],
                geohash: "",
                remark:"口味、偏好等",
            }
        },
        created() {
            if (localStorage.getItem("user_id")) {
                this.isShow1 = false;
            } else {
                this.isShow1 = true;
            }
            if(localStorage.getItem("remark")){
                this.remark = localStorage.getItem("remark");
                let timer = setInterval(function () {
                    localStorage.removeItem("remark");
                    clearInterval(timer);
                },5000)
            }else{
                this.remark = "口味、偏好等";
            }
            this.shop = JSON.parse(localStorage.getItem("shop"));
            this.shops = JSON.parse(localStorage.getItem("shops"));
            this.geohash = JSON.parse(localStorage.getItem("position"))[0].geohash;
            this.user_id = localStorage.getItem("user_id");
            this.cart_id = this.$route.query.cart_id;
            console.log(this.cart_id)
            var a = this.shop.order_lead_time.replace(/小时/,",").replace(/分钟/,"").split(",")
            this.Hours = new Date().getHours();
            this.Minutes = new Date().getMinutes();
            var b = (a[0]*60-0)+(a[1]-0)+this.Hours*60+this.Minutes;
            console.log(b)
            var c = Math.floor(b / 60) % 24;
            var d = b % 60;
            if (c < 10){
                c = "0" + c;
            }
            if (d < 10){
                d = "0" + d;
            }
            this.service_time = c + ":" + d;
            for (let i = 0;i<this.shops.length;i++){
                if(this.shops[i].restaurant_id == this.shop.id){
                    this.total -= 0;
                    this.total += this.shops[i].quantity * this.shops[i].price - 0;
                    this.shopsC.push(this.shops[i]);
                }
            }
            this.total = this.total + 351 + this.shop.float_delivery_fee;
            if (localStorage.getItem("chooseAddress")){
                this.address = JSON.parse(localStorage.getItem("chooseAddress"));
                var time = setInterval(()=>{
                    localStorage.removeItem("chooseAddress");
                    clearInterval(time);
                },500)

                this.isShow3 = false;
            } else{
                this.axios.get("/v1/users/"+this.user_id+"/addresses").then(res=>{
                    if (res.length>0){
                        this.address = res[0];
                        this.isShow3 = false;
                    }
                }).catch(err=>{})
            }

            this.entities = [];
            let obj=[];
            for (let i=0;i<this.shops.length;i++){
                obj.push({
                    attrs:[],
                    extra:{},
                    id:this.shops[i].id,
                    name:this.shops[i].name,
                    packing_fee:this.shops[i].packing_fee,
                    price:this.shops[i].price,
                    quantity:this.shops[i].quantity,
                    sku_id:this.shops[i].sku_id,
                    specs:this.shops[i].specs,
                    stock:this.shops[i].stock,
                })
            }
            this.entities.push(obj);
            console.log(this.entities);
        },
        methods:{
            order(){
                this.axios.post("/v1/users/"+this.user_id+"/carts/"+this.cart_id+"/orders",{
                    address_id:this.address.id,
                    restaurant_id:this.shop.id,
                    geohash:this.geohash,
                    description:"",
                    entities:this.entities,
                }).then(res=>{
                    console.log(res);
                    if(res.status ==1 ){
                        this.$router.push("/payment");
                    }
                })
                let shopS = JSON.parse(localStorage.getItem("shops"));
                if(localStorage.getItem("shops")){
                    for(let i = 0;i< shopS.length; i++){
                        if(this.shop.id == shopS[i].restaurant_id){
                            shopS.splice(i,1);
                            i--;
                        }
                    }
                }
                localStorage.setItem("shops",JSON.stringify(shopS));
            },

        }
    }
</script>

